figure.highlight {
 margin: 0em 0em 1em 0em !important;
 text-align: left;
}

pre.lineno{
 text-align: right;
 color: rgb(245,245,245);
 border-right: 0.1em solid rgb(245, 245, 245) ;
 padding: 0 0.5em 0 0;
 -webkit-touch-callout: none;
 -webkit-user-select: none;
 -khtml-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none;
}

figure.highlight pre{
 padding: 1em 0.5em 1em 0.5em !important;
}

td.code pre{
 padding: 0 0 0 0.5em;
}

tr td.gutter.gl, td.code {
 padding: 0 !important;
}

td.rouge-code pre {
 padding: 0;
}

td.rouge-code{
 padding-left: 0 !important;
}

div.highlight pre.highlight{
 padding:0.5em 0 0.5em 0;
}

div.highlighter-rouge div.highlight{
 margin-bottom: 1em;
}

tr td.rouge-gutter, td.gutter.gl{
 width: 1%;
}


/*
Configuracion de estilos para los modales de permisos
*/
div.klaro.cm-as-context-notice{
 bottom: 0;
 left: 0;
 position: absolute;
 right: 0;
 top: 0;
 height: 50%;
}

figure.image.mb-0.is-16by9 div{
 bottom: 0;
 left: 0;
 position: absolute;
 right: 0;
 top: 0;
}

.subir{
 position: fixed;
 bottom: 5%;
 right: 5%;
 z-index: 50;
 box-shadow: 0 .5em 1em -.125em rgba(10,10,10,.1),0 0 0 1px rgba(10,10,10,.02);
}

/*
Hace que el índice se mantuviera stick pero genera muchos problemas
.indice{
 position: -webkit-sticky;
 position: sticky;
 top: 2vh;
 z-index: 20;
}
*/

details summary {
 border-radius: 4px 4px 4px 4px !important;
}

details[open] summary {
 border-radius: 4px 4px 0px 0px !important;
}

/*
MArgen para las anchor cuando haces scroll
https://css-tricks.com/fixed-headers-on-page-links-and-overlapping-content-oh-my/
*/

html{
 scroll-padding-top: 4em;
 scrollbar-color: hsl(0, 0%, 29%) hsl(0, 0%, 86%);
 scrollbar-width: auto;
}

.menu-list li.is-active a{
 background-color: #3e8ed0;
 color: #fff !important;
}

.menu-list li a:hover{
 background-color: #3e8ed0;
 color: #fff !important;
}

#toc_menu, #toc_menu > li > ul{
 list-style: none;
}

details.message a{
 text-decoration: none !important;
}

.sombra{
 box-shadow: 0 0.5em 1em -0.125em rgba(10, 10, 10, 0.1), 0 0px 0 1px rgba(10, 10, 10, 0.02);
}

.language-text{
 color: rgb(219, 219, 219);
}

pre > code {
 color: rgb(219, 219, 219);
}

.indice_pin{
 position: fixed;
 top: 5%;
 right: 5%;
 z-index: 40;

}

.indice_pin details[open] > div.menu{
 height: 30vh;
 overflow-y: auto;
}

.level {
 width: 100%;
}
.fa-thumbtack{
 color: #fff;
}

.fa-thumbtack:hover{
 color: #FFA552;
}
.fa-thumbtack.activado:hover{
 color: #FFA552;
}

.fa-thumbtack.activado{
 color: #fff;
 transform: rotate(90deg);
}

.navbar-menu{
 background-color: hsl(0, 0%, 21%);
}

a.navbar-item{
 color: hsl(0, 0%, 96%);
}

.img-centrada{
 width: 50%;
 margin-left: auto !important;
 margin-right: auto !important;
}
